import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Setup from "../views/Setup.vue";
import Gameplay from "../views/Gameplay.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/setup",
    name: "Setup",
    component: Setup
  },
  {
    path: "/gameplay",
    name: "Gameplay",
    component: Gameplay
  }
];

const router = new VueRouter({
  routes
});

export default router;
