<template>
  <v-card min-height="480px" width="100%">
    <movimento
      :entregaFeita="entregaFeita"
      @entrega="$emit('entrega')"
    ></movimento>

    <v-divider class="mx-3 pb-3"></v-divider>

    <trabalhadores
      v-if="!trabalhadorEscolhido"
      :vaq="true"
      :con="true"
      :maq="true"
      @usarTrabalhador="$emit('usarTrabalhador', $event)"
    >
      Pegue o trabalhador mais barato. Em caso de empate, escolha à partir do
      topo e da esquerda.
    </trabalhadores>

    <v-row>
      <v-btn class="mx-auto" v-if="trabalhadorEscolhido" @click="$emit('proximo')" small dark tile depressed>
        Próximo
      </v-btn>
    </v-row>    
  </v-card>
</template>

<script>
import Movimento from "./Movimento.vue";
import Trabalhadores from "./Trabalhadores.vue";

export default {
  components: {
    movimento: Movimento,
    trabalhadores: Trabalhadores
  },
  props: {
    entregaFeita: { type: Boolean, required: true },
    trabalhadorEscolhido: { type: Boolean, required: true }
  }
};
</script>
