import Vue from "vue";
import App from "./App.vue";
import "./registerServiceWorker";
import router from "./router";
import TextFilters from "./filters/text-filters";
import vuetify from "./plugins/vuetify";

TextFilters.install();

Vue.config.productionTip = false;

new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount("#app");
