<script>
export default {
  data: () => ({
    dificuldade: "",
    construcoes: []
  }),
  created() {
    this.dificuldade = this.$route.params.dificuldade;
    this.construcoes = this.sortearConstrucoes();
  },
  methods: {
    alerta(msg) {
      alert(msg);
    },
    sortearConstrucoes() {
      var opcoes = ["a", "b"];
      var l = [];
      for (var i = 1; i <= 10; i++) {
        var r = Math.round(Math.random());
        l.push(i + opcoes[r]);
      }
      return l;
    }
  }
};
