<template>
  <v-container id="home" class="d-flex flex-column grey lighten-4 justify-space-between" fill-height>
    <div class="d-flex flex-column">
      <h3 class="text-center title">
        Bem vindo ao Automata Brisco para Great Western Trail
      </h3>
      <v-btn class="mx-auto my-3" dark tile depressed @click="installer()" :style="{'display': installBtn}">Instalar</v-btn>
    </div>
    
    <div  class="d-flex flex-column">
      <h3 class="subtitle">Selecione o nível de dificuldade:</h3>
      <v-radio-group v-model="selecionada">
        <v-radio label="Fácil" value="fácil" color="green"></v-radio>
        <v-radio label="Médio" value="médio" color="yellow"></v-radio>
        <v-radio label="Difícil" value="difícil" color="red"></v-radio>
      </v-radio-group>
    </div>  
    
    <div class="d-flex flex-column">
      <v-btn class="ml-auto" dark tile depressed :to="{ name: 'Setup', params: { dificuldade: selecionada } }">Próximo</v-btn>      
    </div>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    dificuldades: ["fácil", "médio", "difícil"],
    selecionada: "fácil",
    installer: undefined,
    installBtn: "none"
  }),
  created() {
    console.log("CREATED HOME");
    let installPrompt;

    window.addEventListener("beforeinstallprompt", e => {
      console.log("beforeInstallPrompt");
      e.preventDefault();
      installPrompt = e;
      this.installBtn = "block";
    });

    this.installer = () => {
      this.installBtn = "none";
      installPrompt.prompt();
      installPrompt.userChoice.then(result => {
        if(result.outcome === "accepted") {
          console.log("User accepted");
        } else {
          console.log("User denied");
        }
        installPrompt = null;
      });
    };
  }
};
</script>